import {
  Card,
  Image,
  Text,
  Badge,
  Group,
  useMantineTheme,
  createStyles,
  keyframes,
  Button,
  Center,
  SimpleGrid,
  Space,
} from "@mantine/core";
import { ArrowBack } from "tabler-icons-react";
import thumbnail_snsf from "../assets/images/thumbnail_snsf.jpg";
import thumbnail_bayes from "../assets/images/thumbnail_bayes.jpg";
import { Link } from "react-router-dom";

// Export animation to reuse it in other components
export const rise = keyframes({
  from: { transform: "translateY(10%)", opacity: 0 },
  to: { transform: "translateY(0)", opacity: 1 },
});

const useStyles = createStyles((theme, _params, getRef) => ({
  wholeGrid: {
    width: "60%",
    // marginTop: "10%",
    marginBottom: 0,
    marginLeft: "auto",
    marginRight: "auto",
    animation: `${rise} 1s ease-out 1`,
  },

  GridItem: {
    backgroundColor: "rgba(49, 41, 60, 0.8)",
    borderRadius: "5%",
    boxSizing: "border-box",
    border: "1px solid rgba(255, 255, 255, 0.25)",
    boxShadow: "0px 0px 4px rgba(255, 255, 255, 0.2)",
    transition: "300ms",
    color: "rgb(255, 255, 255)",
    height: "100%",
    "&:hover": {
      backgroundColor: "rgba(49, 41, 60, 0.8)",
      transform: "translateY(-3.5%) scale(1.01)",
      zIndex: 100,
      boxShadow: "8px 8px 8px rgba(232, 232, 232, 0.4)",
    },
  },

  ButtonItem: {
    marginTop: "10%",
    backgroundColor: "rgba(255, 255, 255, 0.0)",
    opacity: "0.8",
    transition: "500ms",
    color: "rgb(245, 249, 255)",
    border: "1px solid rgba(237, 245, 255, 0.9)",
    animation: `${rise} 1s ease-out 1`,
    marginBottom: "25%",
    "&:hover": {
      backgroundColor: "rgb(255, 255, 255)",
      color: "rgb(13, 13, 13)",
      opacity: 1,
      zIndex: 100,
    },
  },
}));

function ModalNotebook(image, link, title, t1, t2, t3, t4, text) {
  const theme = useMantineTheme();
  const { classes } = useStyles();
  // const secondaryColor =
  //   theme.colorScheme === "dark" ? theme.colors.dark[1] : theme.colors.gray[7];
  const secondaryColor = theme.colors.dark[0];

  return (
    <>
      <Card
        shadow="sm"
        p="lg"
        className={classes.GridItem}
        component="a"
        href={link}
        target="_blank"
      >
        <Card.Section>
          <Image src={image} height={160} />
        </Card.Section>

        <Group
          // position="apart"
          spacing="xs"
          style={{ marginBottom: 5, marginTop: theme.spacing.sm }}
        >
          <Text weight={500}>{title}</Text>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t1}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t2}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t3}
          </Badge>
          <Badge radius="xs" style={{ color: "#1D3557" }} variant="light">
            {t4}
          </Badge>
        </Group>

        <Text size="sm" style={{ color: secondaryColor, lineHeight: 1.5 }}>
          {text}
        </Text>
      </Card>
    </>
  );
}

export function ScrollytellingContainer({ children }) {
  const { classes } = useStyles();
  return (
    <>
      <Space h={100} />
      <SimpleGrid
        className={classes.wholeGrid}
        cols={4}
        spacing="lg"
        breakpoints={[
          { maxWidth: 1500, cols: 2, spacing: "md" },
          { maxWidth: 755, cols: 1, spacing: "sm" },
          // { maxWidth: 600, cols: 1, spacing: "sm" },
        ]}
      >
        {ModalNotebook(
          thumbnail_snsf,
          "https://colasdroin.github.io/snsf-dataviz/",
          "How is Swiss research funded? A visual story about the Swiss National Science Foundation",
          "React",
          "Python",
          "D3",
          "GSAP",
          "In this work, I visually and interactively explore probability concepts, focusing on Bayes' theorem. Using scroll-driven animations, interactive charts, and responsive math equations, I illustrate key ideas like probability rules and conditional probabilities."
        )}
        {ModalNotebook(
          thumbnail_bayes,
          "https://colasdroin.github.io/demystifying_bayesian_inference/",
          "Demystifying Bayesian Inference. Part 1: A visual introduction to Bayes' theorem",
          "React",
          "Python",
          "D3",
          "React-Spring",
          "In this work, I present a visual exploration of Swiss research funding, focusing on grants awarded by the Swiss National Science Foundation (SNSF). Through interactive charts, scroll-driven animations, and data visualizations, I analyze how research funding is distributed across disciplines, institutions, and time."
        )}
      </SimpleGrid>

      <Center>
        <Link to={"/"}>
          <Button
            leftIcon={<ArrowBack size={18} />}
            className={classes.ButtonItem}
          >
            Back to homepage
          </Button>
        </Link>
      </Center>
    </>
  );
}
