import { TopGithubLink, TopGithubIcon } from "./styles/header";

export default function Header({ children, ...restProps }) {
  return null;
}

Header.TopGithubLink = function HeaderTopGithubLink({ href, children, ...restProps }) {
  return (
    <TopGithubLink href={href} target="_blank" rel="noreferrer" {...restProps}>
      {children}
      <TopGithubIcon src="/icons/github.svg" />
    </TopGithubLink>
  );
};
