import Sketch from "react-p5";
import { useEffect } from "react";
import "./background.css"; // We'll create this CSS file

export default function Background() {
  let waves = [];
  let N = 80;
  let xscale = 0.003;
  let yspeed = 0.001;
  let cnv;

  // Add an effect to add a CSS-based background to the parent container
  useEffect(() => {
    // Add the background-container class to the parent element
    const parentElement = document.getElementById("root") || document.body;
    parentElement.classList.add("background-container");

    return () => {
      // Clean up by removing the class when component unmounts
      parentElement.classList.remove("background-container");
    };
  }, []);

  const setup = (p5, canvasParentRef) => {
    cnv = p5
      .createCanvas(p5.windowWidth, p5.windowHeight)
      .parent(canvasParentRef);
    cnv.position(0, 0, "fixed"); // Change to fixed instead of absolute
    cnv.style("z-index", "-10000"); // Ensure it's way below other content
    cnv.style("pointer-events", "none"); // Make the canvas ignore mouse events
    p5.clear(); // Start with a transparent canvas
    p5.strokeWeight(10);
    p5.stroke(255);
    p5.noFill();
  };

  const draw = (p5) => {
    p5.clear(); // Clear to transparent, not to a color

    waves = []; // Reset waves array to avoid continuously growing
    for (var i = 0; i < N; i++) {
      let y_attn = p5.map(i, 0, N, p5.height * 0.001, p5.height * 0.3);
      const hue = p5.map(i, 0, N, 0, 360);
      waves.push(new NoisySine(xscale, yspeed, y_attn, i * 0.01, hue));
    }

    for (var j = 0; j < N; j++) {
      waves[j].display(p5, 0, 9, 1);
      waves[j].display(p5, p5.PI / 2, 1.7, -1);
    }
  };

  const windowResized = (p5) => {
    p5.resizeCanvas(p5.windowWidth, p5.windowHeight);
  };

  function NoisySine(x_scale_, y_speed_, y_attn_, offset_, hue_) {
    this.N = 256;
    this.x_scale = x_scale_;
    this.y_speed = y_speed_;
    this.y_attn = y_attn_;
    this.offset = offset_;
    this.hue = hue_; // Store the fixed hue value
  }

  NoisySine.prototype.display = function (p5, phase, height_div, dir) {
    p5.push();

    // Use the fixed hue value instead of calculating it based on frameCount
    // Modified to include 128 as alpha value (50% opacity)
    const rainbow = p5.color(p5.map(this.hue, 0, 360, 0, 255), 200, 255, 12);
    p5.stroke(rainbow);

    // Adjusted translation to make bottom wave lower when height_div is small (for bottom wave)
    const yPosition =
      height_div < 2
        ? (1.4 * p5.height) / height_div + 0.05 * p5.width
        : (0.5 * p5.height) / height_div + 0.05 * p5.width;
    p5.translate(0, yPosition);

    p5.beginShape();
    for (var i = 0; i < this.N; i++) {
      let x = p5.norm(i, 0, this.N) * p5.width;
      let y_off = p5.lerp(
        -this.y_attn,
        this.y_attn,
        p5.noise(x * this.x_scale, p5.frameCount * this.y_speed + this.offset)
      );
      let y =
        (-p5.height / 24) * p5.sin(p5.norm(i, 0, this.N) * p5.TWO_PI + phase) +
        dir * y_off;
      p5.curveVertex(x, y);
    }
    p5.endShape();
    p5.pop();
  };
  return <Sketch setup={setup} draw={draw} windowResized={windowResized} />;
}
